import * as React from 'react';
import { useLocation } from '@gatsbyjs/reach-router';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import Container from '@mui/material/Container';
import CssBaseline from '@mui/material/CssBaseline';
import Typography from '@mui/material/Typography';

const theme = createTheme({
  palette: {
    primary: {
      main: '#393c41',
      contrastText: '#393c41'
    },
    background: {
      default: '#eee'
    }
  },
  typography: {
    fontSize: 12,
    fontFamily: ['Noto Serif JP', 'YuMincho', 'serif'].join(','),
    h4: {
      fontSize: 32,
      letterSpacing: 1.5
    },
    h5: {
      fontSize: 28
    }
  },
  components: {
    MuiLink: {
      styleOverrides: {
        root: {
          textDecoration: 'none'
        }
      }
    }
  }
});

interface IProps {
  children: any;
}

export const Layout = ({ children }: IProps): React.JSX.Element => {
  const location = useLocation();
  // const isTop: boolean = location.pathname === '/';

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Container maxWidth='lg'>
        {children}
        <Typography
          align='center'
          mb={2}
          sx={{
            fontSize: 10
          }}
        >
          &copy; YUHEI ONO
        </Typography>
      </Container>
    </ThemeProvider>
  );
};
