import * as React from 'react';
import { navigate } from 'gatsby';
import { useLocation } from '@gatsbyjs/reach-router';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import { Link } from './common/link';

interface IMenuList {
  label: string;
  link: string;
  isExternal?: boolean;
}

const menuLists: IMenuList[] = [
  {
    label: 'Projects',
    link: '/#projects'
  },
  {
    label: 'Works',
    link: '/works/?category=hotel'
  },
  {
    label: 'Profile',
    link: '/profile/'
  },
  {
    label: 'Contact',
    link: '/contact/'
  },
  {
    label: '|',
    link: ''
  },
  {
    label: 'Instagram',
    link: 'https://www.instagram.com/yuheiono.photo/',
    isExternal: true
  }
];

export const Navigation = (): React.JSX.Element => {
  const location = useLocation();

  const [isOpen, setIsOpen] = React.useState<boolean>(false);
  const [translateY, setTranslateY] = React.useState<number>(0);
  const prevScrollTop = React.useRef<number>(0);

  const navigationHeight: number = 80;

  React.useEffect(() => {
    prevScrollTop.current = document.documentElement.scrollTop;
  }, []);

  React.useEffect(() => {
    const handleScroll = () => {
      // 
      if (isOpen) {
        setTranslateY(0);
      } else if (document.documentElement.scrollTop <= 0) {
        setTranslateY(0);
      } else if (document.documentElement.scrollTop < prevScrollTop.current) {
        // 
        setTranslateY(0);
      } else {
        // 
        setTranslateY(navigationHeight * -1);
      }

      prevScrollTop.current = document.documentElement.scrollTop;
    };

    window.addEventListener('scroll', handleScroll);

    return () => {
      return window.removeEventListener('scroll', handleScroll);
    };
  }, [isOpen]);

  const toggleDrawer = (_isOpen: boolean) => (event: any) => {
    if (
      event &&
      event.type === 'keydown' &&
      (event.key === 'Tab' || event.key === 'Shift')
    ) {
      return;
    }

    setIsOpen(_isOpen);
  };

  return (
    <Box>
      <AppBar
        position='fixed'
        sx={{
          backgroundColor: 'background.default',
          boxShadow: 'none',
          transition: 'transform 250ms ease-out',
          transformOrigin: 'top center',
          transform: `translateY(${translateY}px)`
        }}
      >
        <Container>
          <Box
            sx={{
              display: 'flex',
              justifyContent: 'space-between',
              alignItems: 'center',
              height: navigationHeight
            }}
          >
            <Box>
              <Link
                href='/'
                sx={{
                  display: 'block'
                }}
              >
                <Typography
                  sx={{
                    letterSpacing: '0.3rem'
                  }}
                >
                  YUHEI ONO
                </Typography>
              </Link>
            </Box>
            <Box display='flex'>
              {menuLists.map(({ label, link, isExternal }, i) => {
                let isCurrent = false;

                if (link === location.pathname) {
                  isCurrent = true;
                }

                /*
              @TODO works 

              else if (
                !!location.pathname.match(/^(\/works\/)/) &&
                link === '/works/'
              ) {
                // works 
                isCurrent = true;
              }
              */

                return link ? (
                  <Link
                    key={i}
                    href={link}
                    sx={{
                      ml: '1.5rem',
                      display: {
                        xs: 'none',
                        sm: 'block'
                      },
                      textDecoration: isCurrent && 'underline',
                      pointerEvents: isCurrent && 'none'
                    }}
                  >
                    <Typography
                      variant='body2'
                      sx={{
                        letterSpacing: '0.1rem'
                      }}
                    >
                      {label}
                    </Typography>
                  </Link>
                ) : (
                  <Typography
                    key={i}
                    variant='body2'
                    sx={{
                      pointerEvents: 'none',
                      ml: '1.5rem',
                      display: {
                        sm: 'block',
                        xs: 'none'
                      }
                    }}
                  >
                    {label}
                  </Typography>
                );
              })}

              <Box
                sx={{
                  display: {
                    sm: 'none',
                    xs: 'flex'
                  },
                  alignItems: 'center',
                  cursor: 'pointer'
                }}
                onClick={toggleDrawer(!isOpen)}
              >
                <Box
                  display='flex'
                  justifyContent='space-between'
                  flexDirection='column'
                  sx={{
                    width: 24,
                    height: 16
                  }}
                >
                  {[
                    {
                      transformOrigin: 'left top',
                      transform: `rotate(${isOpen ? 45 : 0}deg)`
                    },
                    {
                      ml: 0,
                      opacity: isOpen ? 0 : 1
                    },
                    {
                      transformOrigin: 'left bottom',
                      transform: `rotate(${isOpen ? -45 : 0}deg)`
                    }
                  ].map((sx, i) => {
                    return (
                      <Box
                        key={i}
                        sx={{
                          transition: 'all 250ms ease-out',
                          ml: isOpen ? '6px' : '0px',
                          width: isOpen ? 21 : 24,
                          height: '1px',
                          bgcolor: 'primary.main',
                          ...sx
                        }}
                      />
                    );
                  })}
                </Box>
              </Box>
            </Box>
          </Box>
        </Container>
      </AppBar>

      <Box
        sx={{
          transition: 'all 500ms ease-out',
          pointerEvents: isOpen ? 'auto' : 'none',
          opacity: isOpen ? 1 : 0,
          zIndex: isOpen ? 999 : -1,
          position: 'fixed',
          width: '100%',
          height: '100%',
          top: 0,
          bottom: 0,
          left: 0,
          right: 0,
          backgroundColor: 'background.default',
          overflowY: 'scroll',
          overscrollBehavior: 'none'
        }}
      >
        <Box
          sx={{
            width: '100%',
            height: '100%',
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            zIndex: isOpen ? 999 : -1
          }}
        >
          <Box
            display='flex'
            justifyContent='center'
            sx={{
              pt: `${navigationHeight}px`,
              overflowY: 'scroll',
              margin: '0 auto'
            }}
          >
            <Box>
              {menuLists
                .filter(({ link }) => !!link)
                .map(({ label, link, isExternal }, i) => {
                  return (
                    <Box key={i} my={4} sx={{ textAlign: 'center' }}>
                      <Link
                        href={link}
                        onClick={(e: any) => {
                          if (!isExternal) {
                            e.preventDefault();
                            navigate(link);
                          }
                        }}
                      >
                        <Typography
                          variant='body1'
                          sx={{
                            textAlign: 'center'
                          }}
                        >
                          {label}
                        </Typography>
                      </Link>
                    </Box>
                  );
                })}
            </Box>
          </Box>
        </Box>
      </Box>
    </Box>
  );
};
