import * as React from 'react';
import { navigate } from 'gatsby';
import MuiLink from '@mui/material/Link';

interface IProps {
  children: any;
  href: string;
  onClick?: React.MouseEventHandler<HTMLAnchorElement>;
  sx?: any;
}

export const Link = (props: IProps): React.JSX.Element => {
  const { children, href, sx } = props;

  const isInternal: boolean = /^\/(?!\/)/.test(href) || /^#/.test(href);
  const isBlank: boolean = !isInternal;

  const [isMouseOver, setIsMouseOver] = React.useState<boolean>(false);

  const onClick: React.MouseEventHandler<HTMLAnchorElement> = (
    e: React.MouseEvent<HTMLAnchorElement>
  ) => {
    window.localStorage.setItem('scrollY', String(0));

    if (!isBlank) {
      e.preventDefault();
      navigate(href);
    }
  };

  return (
    <MuiLink
      {...props}
      sx={{
        transitionProperty: 'filter',
        transitionDuration: '300ms',
        transitionTimingFunction: 'ease-out',
        filter: isMouseOver ? 'brightness(0.75)' : 'brightness(1.0)',
        ...sx
      }}
      href={href}
      target={isBlank ? '_blank' : '_self'}
      rel={isBlank ? 'noopener noreferrer' : ''}
      onClick={props.onClick ? props.onClick : onClick}
      onMouseOver={() => {
        setIsMouseOver(true);
      }}
      onMouseLeave={() => {
        setIsMouseOver(false);
      }}
    >
      {children}
    </MuiLink>
  );
};
